.slick {

  &-slider {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &-list {
    width: calc(100% - 144px);
  }

  &-track {
    // display: flex;
    // overflow: hidden;

    width: 100%;
  }

  &-next, &-prev {
    position: static;
    width: 72px;
    height: 72px;
    border-radius: 50%;
    background: none;
    border: 1px solid var(--elements);

    &::before {
      display: none;
    }
  }

  &-next {
    &::before {
      content: "";
      display: block;
    }
  }
}