.modal {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);

  &__item {
    position: absolute;
    height: max-content;
    padding: 42px;
    width: 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #fff;

    &__close {
      position: absolute;
      top: 42px;
      right: 42px;
    }
  }
}