.home {

  &__info {
    display: flex;
    gap: 48px;
    justify-content: space-between;

    &__content {
      min-width: 1/3 * 100%;
      max-width: 500px;
    }

    &__title {
      position: relative;
    }

    &__text {
      margin-bottom: 92px;
    }

    &__button {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 3/5 * 100%;
      font-style: normal;
      font-weight: 400;
      font-size: 20px;
      line-height: 27px;
      height: 48px;
      background: var(--buttonBg);
    }
  }


  &__hit {
    display: flex;
    flex-direction: column;
  }
}