.slider-vertical {
  display: flex;
  gap: 95px;
  align-items: center;
  justify-content: space-between;
  position: relative;

  &__list {
    overflow: hidden;
    max-height: 480px;
    width: 160px;

    // height: 100px;
    // background: rgba(255, 255, 255, 0.24);
    // filter: blur(89.5px);

    // backdrop-filter: blur(162px);

    // background: #FFFFFF;
    // filter: blur(162px);
    z-index: 0;
  }

  &__blur {
    --side: 160px;
    position: absolute;
    width: var(--side);
    height: var(--side);
    top: 50%;
    left: 0;
    transform: translate(0, -50%);
    background: #FFFFFF;
    filter: blur(162px);
  }

  &__nav {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 12px;
  }

  &__dots {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  &__dot {
    display: block;
    width: 12px;
    height: 12px;
    border: 1px solid var(--text);
    border-radius: 50%;
    transition: 0.1s;

    &--active {
      border: none;
      background: var(--elements);
    }
  }

  &__track {
    display: flex;
    flex-direction: column;
    transition: 0.7s;
    z-index: 10000;
    // display: none;
  }

  &__item {
    height: 480px;
    width: 160px;
    background-size: cover;
    background-position: center;
    
  }

  &__info {
    display: grid;
    grid-template-rows: auto 100px ;
    gap: 32px;
    // flex-grow: 2;
    width: 400px;

    &__button {
      max-width: max-content;
      // position: relative;
    }

    &__button::after {
      content: "";
      display: block;
      width: 100%;
      height: 1px;
      // position: relative;
      background: #000;
    }

    &__price {
      font-weight: 600;
      font-size: 24px;
      line-height: 22px;
      /* or 92% */


      color: #000000;
    }
  }
}